import { Layout, Name, Planner, Welcome } from "./components";
import { useState } from "react";
import { VIEWS } from "./utils/contstants";

function App() {
  const [activeView, setActiveView] = useState(VIEWS.welcome);

  return (
    <Layout>
      {activeView === VIEWS.welcome && <Welcome onSetView={setActiveView} />}
      {activeView === VIEWS.name && <Name onSetView={setActiveView} />}
      {activeView === VIEWS.planner && <Planner />}
    </Layout>
  );
}

export default App;
