import React, { useState } from "react";
import {
  PlannerContainer,
  PlannerTitle,
  PlannerText,
  PlannerWrapper,
} from "./planner.styled";
import { Button, Input } from "../UI";

import { Chart as ChartJS, defaults } from "chart.js/auto";
import { Bar, Doughnut, Line } from "react-chartjs-2";

defaults.maintainAspectRatio = false;
defaults.responsive = true;

defaults.plugins.title.display = true;
defaults.plugins.title.align = "center";
defaults.plugins.title.font.size = 20;
defaults.plugins.title.color = "black";

export const Planner = () => {
  const [isShowTips, setIsShowTips] = useState(false);
  const [isShowMessage, setIsShowMessage] = useState(false);
  const [budget, setBudget] = useState("10000");
  const [sourceData, setSourceData] = useState([
    {
      label: "Food",
      value: 20000,
    },
    {
      label: "Flat",
      value: 30000,
    },
    {
      label: "Investing",
      value: 50000,
    },
  ]);

  const handleClick = () => {
    setIsShowTips(true);
    const count = budget / 10;
    const food = count * 2;
    const flat = count * 3;
    const investing = count * 5;

    setSourceData((prevSourceData) => {
      return prevSourceData.map((data) => {
        return {
          ...data,
          value:
            data.label === "Food"
              ? food
              : data.label === "Flat"
                ? flat
                : investing,
        };
      });
    });
  };

  const handleClickTips = () => {
    setIsShowTips(false);
    setIsShowMessage(true);
  };

  return (
    <PlannerContainer>
      <PlannerTitle>Your money planner for this month, June 2024</PlannerTitle>
      <PlannerText>Enter your budget:</PlannerText>
      <Input
        margin="8px 0 20px"
        type="text"
        value={budget}
        onChange={(e) => setBudget(e.target.value)}
      />
      <Button onClick={handleClick}>Calculate</Button>

      <PlannerWrapper>
        <Doughnut
          data={{
            labels: sourceData.map((data) => data.label),
            datasets: [
              {
                label: "Count",
                data: sourceData.map((data) => data.value),
                backgroundColor: [
                  "rgba(43, 63, 229, 0.8)",
                  "rgba(250, 192, 19, 0.8)",
                  "rgba(253, 135, 135, 0.8)",
                ],
                borderColor: [
                  "rgba(43, 63, 229, 0.8)",
                  "rgba(250, 192, 19, 0.8)",
                  "rgba(253, 135, 135, 0.8)",
                ],
              },
            ],
          }}
          options={{
            plugins: {
              title: {
                text: "Revenue Sources",
              },
            },
          }}
        />
      </PlannerWrapper>
      {isShowTips && (
        <>
          <p>Тут ще потрібно добавити витрати і збереження сторінки</p>
          <p>
            Поки не зроблю цього функціоналу, ми нічого не витрачаємо у червні
            =)
          </p>
          <Button onClick={handleClickTips}>Нажми мене</Button>
        </>
      )}

      {isShowMessage && (
        <h2
          style={{
            color: "#fe9c8e",
            textAlign: "center",
            fontSize: "25px",
          }}
        >
          Я тебе кохаю, моя княгиня Ольга ❤❤❤
        </h2>
      )}
    </PlannerContainer>
  );
};
