import React, { useState } from "react";
import { NameContainer, NameText, NameTitle } from "./name.styled";
import { Button, Input } from "../UI";
import { VIEWS } from "../../utils/contstants";

export const Name = ({ onSetView }) => {
  const [name, setName] = useState("");

  const handleClick = () => {
    onSetView(VIEWS.planner);
  };
  return (
    <NameContainer>
      <NameTitle>Let's get to know each other</NameTitle>
      <NameText>Enter your name:</NameText>
      <Input
        margin="8px 0 20px"
        type="text"
        value={name}
        onChange={(e) => setName(e.target.value)}
      />
      <Button onClick={handleClick}>Continue</Button>
    </NameContainer>
  );
};
